* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 10px;
  font-weight: 100;
}

html body {
  height: 100vh;
  padding-top: 60px;
}

#header-sec {
  z-index: 1;
  background-color: red;
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
}

#header-sec .page-scroll-bar {
  width: 100%;
  height: 10%;
}

#header-sec .page-scroll-bar progress {
  appearance: none;
  background-color: #000;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  display: block;
}

#header-sec .page-scroll-bar progress::-webkit-progress-bar {
  background-color: #000;
}

#header-sec .page-scroll-bar progress::-webkit-progress-value {
  background-color: #1f9921;
}

#header-sec .page-scroll-bar progress::-moz-progress-bar {
  background-color: #1f9921;
}

#header-sec .nav-bar {
  justify-content: space-evenly;
  align-items: center;
  gap: 20px;
  height: 90%;
  padding-inline: 20px;
  display: flex;
}

#header-sec .nav-bar .greetings, #header-sec .nav-bar .topic-btn .aTag {
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
}

#header-sec .nav-bar .greetings {
  margin-right: auto;
}

#header-sec .nav-bar .topic-btn .aTag {
  cursor: pointer;
  background-color: #000;
  padding: 5px 10px;
  display: block;
}

#header-sec .nav-bar .topic-btn .aTag:hover {
  transform: translateX(2px)translateY(-2px);
  box-shadow: -2px 2px 10px 3px #ffffffa7;
}

#TecSkills-sec-cards {
  background-color: gray;
  grid-template-columns: repeat(auto-fit, minmax(30px, 80px));
  justify-content: center;
  width: 100%;
  margin: auto;
  display: grid;
}

#TecSkills-sec-cards .tech-card {
  cursor: pointer;
  aspect-ratio: 1;
  background-color: #fff;
  position: relative;
  overflow: hidden;
}

#TecSkills-sec-cards .tech-card:hover .tech-name {
  top: 0 !important;
}

#TecSkills-sec-cards .tech-card .tech-img {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
}

#TecSkills-sec-cards .tech-card .tech-img img {
  object-fit: contain;
  object-position: center;
  width: 80%;
}

#TecSkills-sec-cards .tech-card .tech-name {
  backdrop-filter: blur(10px);
  -webkit-user-select: none;
  user-select: none;
  word-break: break-all;
  color: #fff;
  background-color: #00000086;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 15px;
  font-weight: 900;
  transition: top .5s;
  display: flex;
  position: absolute;
  top: 100%;
}
